.image-banner-wrapper{
    width:100%;
    position: relative;
    cursor: pointer;

    .image-banner{
        width:100%;
        border-radius: 7px;
        height: 720px;
        object-fit:cover;
        display: block;
    }

    .overlay-text{
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);

        .main-heading{
            font-size:50px;
        }

        p{
            text-transform: uppercase;
            font-size: 14px;
        }

    }

}